.lowbar {
  position: sticky;
  width: 428px;
  height: 70px;
  bottom: 0px;
  margin: 0 auto;
  margin-top: 10px;
}
.lowbar .navbar {
  width: 428px;
  height: 70px;
  background: #FFFFFF;
  box-shadow: 0px -1px 0.5px rgba(0, 0, 0, 0.2);
  border-radius: 40px 40px 0px 0px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
}
.iconBox {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
}
.iconBox span {
  font-weight: 700;
  font-size: 12px;
  text-align: center;
  margin-top: 5px;
  cursor: pointer;
}
.iconBox .house-icon {
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.iconBox .plus-back {
  content: '';
  position: absolute;
  background-color: #0063C8;
  width: 54px;
  height: 54px;
  border-radius: 27px;
  text-align: center;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.iconBox .plus-back .plus-icon {
  width: 30px;
  height: 30px;
  color: #fff;
  cursor: pointer;
}
.iconBox .calendar-icon {
  width: 30px;
  height: 30px;
  cursor: pointer;
}
