@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
body{
  font-family: 'Roboto', sans-serif;
}
.App {
  width: 428px;
  height: auto;
  min-height: 726px;
  background: linear-gradient(180deg, rgba(222, 242, 255, 0.7) 0%, #F0F2F5 42.98%);
  margin: 0 auto;
}
.App-header {
  width: 428px;
  height: 60px;
  padding: 20px 0px 0px 15px;
}
.App-header h2{
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  color: #399DFC;
}
.schedule {
  width: 400px;
  height: auto;
  min-height: 260px;
  max-height: 410px;
  background-color: #fff;
  margin: 0 auto;
  margin-top: 25px;
  margin-bottom: 20px;
  border-radius: 20px;
  box-shadow: 0px 4px 5px -3px rgba(0, 0, 0, 0.25);
  padding: 18px 17px;
  box-sizing: border-box;
  overflow-y: hidden;
  
}
.time {
  width: 350px;
  height: 25px;
  margin-top: 10px;
  margin-bottom: 20px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.time span {
  font-weight: bold;
}
.time .HOME-NOON{
  margin-left: 10px;
  margin-right: 5px;
}
.time .HOME-HOUR{
  margin-right: 3px;
}
.time .HOME-MIN{
  margin-left: 3px;
}
.bell-icon {
  color: #FA965E;
}
.vitamin-boxs{
  width: 350px;
  height: auto;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
}
.vitamin-boxs .vitamin-box {
  position: relative;
}
.vitamin-off {
  width: 350px;
  height: 50px;
  background: #fff;
  box-sizing: border-box;
  border-radius: 25px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.vitamin-off .vitamin-name {
  font-weight: 700;
  font-size: 24px;
  color: #000;
  margin-left: 27px;
  z-index: 3;
}
.vitamin-off .check-btn {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  margin-right: 5px;
  z-index: 3;
  cursor: pointer;
}
.vitamin-on {
  width: 350px;
  height: 50px;
  background: #fff;
  border: none;
  border-radius: 25px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.vitamin-on .vitamin-name {
  font-weight: 700;
  font-size: 24px;
  /* color: #fff; */
  margin-left: 27px;
  z-index: 3;
  transition: all 0.5s;
}
.vitamin-on .check-btn {
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 20px;
  margin-right: 5px;
  z-index: 3;
  transition: all 0.7s;
}
.vitamin-animation-off {
  height: 50px;
  border-radius: 25px;
  position: absolute;
  top: 0;
  /* transition: all 0.7s; */
}
.vitamin-animation-on{
  height: 50px;
  border-radius: 25px;
  position: absolute;
  top: 0;
  /* transition: all 0.7s; */
}

